.App {
  text-align: center;
}

body {
  background: #FAFAFA;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

a {
  text-decoration: none;
}
  
.wl-container{
  width: 100%;
  margin: 0 auto;
  max-width: 1600px;
}

@media(max-width: 1500px){
  .wl-container {
    max-width: 1200px;
  }
}

@media(max-width: 1400px){
  .wl-container {
    max-width: 1100px;
  }
}

@media(max-width: 1200px){
  .wl-container {
    max-width: 900px;
  }
}

@media(max-width: 992px){
  .wl-container {
    max-width: 290px;
  }
  .desk {
    display: none;
  }
}

@media(min-width: 992px){
  .mob {
    display: none;
  }
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}